<template>
    <DPageContent>
        <el-card shadow="never">
            <el-row class="d-row">
                <div class="d-search-form" style="float: left">
                    <el-select v-model="searchForm.type"
                               size="small"
                               clearable
                               @change="setTableData"
                               placeholder="">
                        <el-option v-for="item in dbTypeList" :label="item.name" :value="item.code" :key="item.code"></el-option>
                    </el-select>
                    <el-input size="small"
                              placeholder="/"
                              suffix-icon="el-icon-search"
                              class="item"
                              clearable
                              v-model="searchForm.name"
                              @change="setTableData"></el-input>
                </div>
                <div style="float: right">
                    <el-button type="primary" size="small" @click="openAddDsForm()"></el-button>
                </div>
            </el-row>
            <el-row class="d-row">
                <el-table :data="tableData.data"
                          row-key="id"
                          default-expand-all
                          :row-class-name="tableRowClassName">
                    <el-table-column
                            prop="name"
                            label=""
                            width="200">
                        <template slot-scope="scope">
                            <router-link :to="'/data-map/ds-manage/' + scope.row.id">{{ scope.row.name }}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label=""
                            width="150">
                    </el-table-column>
                    <el-table-column
                            prop="host"
                            label=""
                            width="200">
                    </el-table-column>
                    <el-table-column
                            prop="dbName"
                            label=""
                            width="200">
                    </el-table-column>
                    <el-table-column
                            prop="remark"
                            label="">
                    </el-table-column>
                    <el-table-column
                            label=""
                            width="250">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="openUpdateDsForm(scope.row)"></el-button>
                            <el-button type="text" size="small" @click="deleteDsById(scope.row)"></el-button>
                            <el-button type="text" size="small" v-if="scope.row.isBaseDs === 1" @click="openAddDsForm(scope.row)"></el-button>
                            <el-button type="text" size="small" v-if="scope.row.isBaseDs === 1" @click="openDsOwnerForm(scope.row)">Owner</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="setTableData"
                        @size-change="setTableData"
                        background
                        :current-page.sync="pageNo"
                        :page-sizes="[10, 20, 50, 100]"
                        :page-size.sync="pageSize"
                        layout="total, sizes, prev, pager, next"
                        :total="tableData.total">
                </el-pagination>
            </el-row>
        </el-card>
        <el-dialog :title="dsFormTitle"
                   top="30px"
                :visible.sync="dsFormVisible">
            <el-form ref="dsForm"
                     :model="dsForm"
                     :rules="formRules"
                     label-width="100px">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="type">
                            <el-select v-model="dsForm.type" :disabled="dsForm.id != undefined" style="width: 100%">
                                <el-option v-for="item in dbTypeList" :label="item.name" :value="item.code" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="dsForm.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="remark">
                            <el-input v-model="dsForm.remark"
                                      type="textarea"
                                      :rows="2"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="host">
                            <el-input v-model="dsForm.host"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="port">
                            <el-input v-model="dsForm.port"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="dbName">
                            <el-input v-model="dsForm.dbName" :disabled="dsForm.id != undefined"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="userName">
                            <el-input v-model="dsForm.userName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="password">
                            <el-input v-model="dsForm.password" type="password"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item v-if="dsForm.isBaseDs === 0" label="" prop="priority">
                            <el-input-number v-model="dsForm.priority" :min="1" :max="100" style="width: 100%"></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item v-if="dsForm.isBaseDs === 0" label="" prop="isAutoMeta">
                            <el-radio-group v-model="dsForm.isAutoMeta">
                                <el-radio :label="1"></el-radio>
                                <el-radio :label="0"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                </el-row>

            </el-form>
            <DSubmitCancel slot="footer" nohr @submit="submitDsForm" @cancel="dsFormVisible = false">
                <el-button type="primary" @click="testDs"></el-button>
            </DSubmitCancel>
        </el-dialog>
        <el-dialog :title="'Owner' + currentDs.name"
                   :visible.sync="dsOwnerFormVisible">
            <el-row style="margin-bottom: 20px">
                <el-select filterable
                           reserve-keyword
                           multiple
                           remote
                           placeholder=","
                           size="small"
                           v-model="userNameList"
                           @change="selectUser"
                           :remote-method="searchUser"
                           style="float: right;width: 220px">
                    <el-option
                            v-for="(item, index) in searchUserList"
                            :key="item.userName"
                            :label="item.userNameCn"
                            :value="item.userName">
                        <span style="float: left">{{ item.userNameCn }}({{item.userName}})</span>
                        <span style="float: right">
                            <el-button type="text"
                                       title=""
                                       icon="el-icon-plus"
                                       @click="addDsOwner(item, index)"></el-button>
                        </span>
                    </el-option>
                </el-select>
            </el-row>
            <el-row>
                <el-col :span="6"
                        v-for="(dsOwner, index) in dsOwnerList"
                        :key="dsOwner.id"
                        class="d-hover-btn">
                    <span>{{dsOwner.ownerNameCn}}({{dsOwner.ownerName}})</span>
                    <el-button type="text"
                               title=""
                               icon="el-icon-delete"
                               @click="deleteDsOwnerById(dsOwner.id, index)"></el-button>
                </el-col>
            </el-row>
        </el-dialog>
    </DPageContent>
</template>

<script>
    import {
        DbType,
        listDs,
        addOrUpdateDs,
        deleteDs,
        testDs,
        listDsOwner,
        deleteDsOwner,
        addDsOwner
    } from '../../../services/data-map/ds-manage'
    import {
        searchUser
    } from '../../../services/auth/user'
    export default {
        name: 'DDsManager',
        data () {
            return {
                searchForm: {
                    type: '',
                    name: ''
                },
                pageSize: 10,
                pageNo: 1,
                tableData: {
                    total: 0,
                    data: []
                },
                dbTypeList: Object.values(DbType),
                dsFormVisible: false,
                dsFormTitle: '',
                dsForm: {
                    type: DbType.MYSQL.code, // 
                    name: '', // 
                    host: '', // 
                    port: '', // 
                    dbName: '', // 
                    userName: '', // 
                    password: '', // 
                    isBaseDs: 1, // 
                    priority: 0 // 
                },
                formRules: {
                    type: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    host: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    dbName: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', pattern: /^[A-Za-z]+[A-Za-z0-9_]*[A-Za-z0-9]+$/, message: ',', trigger: 'blur' }
                    ],
                    userName: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', pattern: /^[A-Za-z]+[A-Za-z0-9_]*[A-Za-z0-9]+$/, message: ',', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    port: [
                        { type: 'string', pattern: /^[1-9]+[0-9]*$/, message: '', trigger: 'blur' }
                    ]
                },
                currentDs: {},
                dsOwnerFormVisible: false,
                dsOwnerList: [],
                searchUserList: [],
                userNameList: []
            }
        },
        methods: {
            setTableData () {
                listDs({
                    pageSize: this.pageSize,
                    pageNo: this.pageNo,
                    type: this.searchForm.type,
                    name: this.searchForm.name
                }).then(res => {
                    this.tableData = res.data
                }).catch(this.$handleError)
            },
            openAddDsForm (row) {
                this.dsFormVisible = true
                this.dsForm = {
                    type: DbType.MYSQL.code, // 
                    name: '', // 
                    host: '', // 
                    port: '', // 
                    dbName: '', // 
                    userName: '', // 
                    password: '', // 
                    priority: 1, // 
                    isAutoMeta: 1 // ,impalahive,kylinhive
                }
                if (row) {
                    this.dsFormTitle = '' + row.name + ' '
                    this.dsForm.isBaseDs = 0
                    this.dsForm.baseDsId = row.id
                } else {
                    this.dsFormTitle = ''
                    this.dsForm.isBaseDs = 1
                    this.dsForm.baseDsId = 0
                }
            },
            openUpdateDsForm (row) {
                this.dsFormVisible = true
                if (row.isBaseDs === 1) {
                    this.dsFormTitle = ''
                } else {
                    this.dsFormTitle = ''
                }
                this.dsForm = {
                    id: row.id,
                    type: row.type, // 
                    name: row.name, // 
                    remark: row.remark, // 
                    host: row.host, // 
                    port: row.port, // 
                    dbName: row.dbName, // 
                    userName: row.userName, // 
                    password: row.password, // 
                    isBaseDs: row.isBaseDs, // 
                    baseDsId: row.baseDsId, // 
                    priority: row.priority, // 
                    isAutoMeta: row.isAutoMeta
                }
            },
            submitDsForm () {
                this.$refs['dsForm'].validate((valid) => {
                    if (valid) {
                        addOrUpdateDs(this.dsForm).then(res => {
                            this.dsFormVisible = false
                            this.setTableData()
                        }).catch(this.$handleError)
                    } else {
                        return false
                    }
                })
            },
            testDs () {
                this.$refs['dsForm'].validate((valid) => {
                    if (valid) {
                        testDs(this.dsForm).then(res => {
                            this.$messageSuccess('!')
                        }).catch(this.$handleError)
                    } else {
                        return false
                    }
                })
            },
            deleteDsById (row) {
                let msg
                if (row.isBaseDs === 1) {
                    msg = '<strong class="d-error">"' + row.name + '"</strong>?'
                } else {
                    msg = '<strong class="d-error">"' + row.name + '"</strong>?'
                }
                let vue = this
                vue.$confirmInfo(msg, function () {
                    deleteDs({
                        id: row.id
                    }).then(res => {
                        vue.setTableData()
                    }).catch(vue.$handleError)
                })
            },
            tableRowClassName({row, rowIndex}) {
                if (row.children && row.children.length) {
                    return 'success-row'
                }
                return ''
            },
            openDsOwnerForm (row) {
                this.currentDs = row
                this.dsOwnerFormVisible = true
                this.setDsOwnerList()
            },
            setDsOwnerList () {
                this.dsOwnerList = []
                listDsOwner({
                    id: this.currentDs.id
                }).then(res => {
                    this.dsOwnerList = res.data
                }).catch(this.$handleError)
            },
            deleteDsOwnerById (dsOwnerId, index) {
                deleteDsOwner({
                    dsOwnerId: dsOwnerId
                }).then(res => {
                    this.dsOwnerList.splice(index, 1)
                }).catch(this.$handleError)
            },
            searchUser (keyword) {
                if (keyword) {
                    searchUser({
                        keyword: keyword
                    }).then(res => {
                        this.searchUserList = res.data.filter(d => {
                            let i = this.dsOwnerList.findIndex(u => u.ownerName === d.userName)
                            return i === -1
                        })
                    }).catch(this.$handleError)
                }
            },
            addDsOwner (user, index) {
                addDsOwner({
                    dsId: this.currentDs.id,
                    ownerName: user.userName,
                    ownerNameCn: user.userNameCn
                }).then(res => {
                    this.dsOwnerList.unshift({
                        id: res.data,
                        ownerName: user.userName,
                        ownerNameCn: user.userNameCn
                    })
                    this.searchUserList.splice(index, 1)
                }).catch(this.$handleError)
            },
            selectUser () {
                this.userNameList = []
            }
        },
        mounted () {
            this.setTableData()
        }
    }
</script>

<style>
    .el-table .success-row {
        background: #f0f9eb;
    }
</style>