import vue from '../main'

export default {
    /**
     * 
     * @param msg
     */
    messageInfo (msg) {
        vue.$message(msg)
    },

    /**
     * 
     * @param msg
     */
    messageSuccess (msg) {
        vue.$message({message: msg, type: 'success'})
    },

    /**
     * 
     * @param msg
     */
    messageWarn (msg) {
        vue.$message({message: msg, type: 'warning'})
    },

    /**
     * 
     * @param msg
     */
    messageError (msg) {
        vue.$message.error(msg)
    },

    /**
     * 
     * @param msg
     */
    notifyInfo (msg) {
        vue.$notify.info({title: '', message: msg})
    },

    /**
     * 
     * @param msg
     */
    notifySuccess (msg) {
        vue.$notify({title: '', message: msg, type: 'success'})
    },

    /**
     * 
     * @param msg
     */
    notifyWarn (msg) {
        vue.$notify({title: '', message: msg, type: 'warning'})
    },

    /**
     * 
     * @param msg
     */
    notifyError (msg) {
        vue.$notify.error({title: '', message: msg})
    },

    /**
     * 
     * @param msg
     */
    alertInfo (msg) {
        vue.$msgbox({title: '', message: msg, type: 'info'})
    },

    /**
     * 
     * @param msg
     */
    alertSuccess (msg) {
        vue.$msgbox({title: '', message: msg, type: 'success'})
    },

    /**
     * 
     * @param msg
     */
    alertWarn (msg) {
        vue.$msgbox({title: '', message: msg, type: 'warning'})
    },

    /**
     * 
     * @param msg
     */
    alertError (msg) {
        vue.$msgbox({title: '', message: msg, type: 'error'})
    },

    /**
     * 
     * @param msg
     * @param confirmCallBack
     * @param cancelCallBack
     */
    confirmInfo (msg, confirmCallBack, cancelCallBack) {
        if (!cancelCallBack) {
            cancelCallBack = function () {}
        }
        vue.$confirm(msg, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            dangerouslyUseHTMLString: true,
            type: 'warning'
        }).then(() => {
            confirmCallBack()
        }).catch(() => {
            cancelCallBack()
        })
    },

  /**
   * 
   */
  goBack () {
    vue.$router.back()
  }
}
